@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: local("Roboto"), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  color: #000000;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.4;
  font-weight: 400;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

main {
  flex-grow: 1;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  min-width: 280px;
  max-width: 1110px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.reset-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.navigation-opened {
  overflow: hidden;
}

.button {
  display: inline-flex;
  padding: 10px 25px 11px 24px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.1;
  font-weight: 400;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-decoration: none;
  background-color: transparent;
  border: 2px solid #ffffff;
  border-radius: 40px;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.button:active {
  opacity: 0.4;
}
.button:focus-visible {
  outline: 2px solid #000000;
  outline-offset: 2px;
}
.button--bordered-accent {
  color: #000000;
  border-color: #66c05d;
}
.button--colored-accent {
  color: #ffffff;
  background-color: #66c05d;
  border-color: #66c05d;
  border-radius: 30px;
}

.navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (max-width: 767px) {
  .navigation {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.navigation__item {
  padding: 5px 0;
}
.navigation__item:not(:last-child) {
  margin-right: 30px;
}
@media (max-width: 767px) {
  .navigation__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.navigation--catalog .navigation__item:not(:last-child) {
  margin-right: 40px;
}
@media (max-width: 767px) {
  .navigation--catalog .navigation__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.navigation__link {
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .navigation__link:hover:not(.focus-visible) {
    text-decoration: underline;
  }
}
.navigation__link:active {
  opacity: 0.6;
}
.navigation__link:focus-visible {
  outline: 2px solid #000000;
  outline-offset: 2px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__image {
  margin-right: 20px;
  width: 80px;
  height: 80px;
}
@media (max-width: 1199px) {
  .logo__image {
    width: 60px;
    height: 60px;
  }
}
@media (max-width: 767px) {
  .logo__image {
    width: 40px;
    min-width: 40px;
    height: 40px;
    min-height: 40px;
    margin-right: 0;
  }
}

.logo__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 36px;
  line-height: 1.16;
  font-weight: 700;
  color: #66c05d;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}
@media (max-width: 1199px) {
  .logo__title {
    font-size: 32px;
  }
}
@media (max-width: 767px) {
  .logo__title {
    display: none;
  }
}

.back-call {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .back-call {
    flex-direction: column;
    align-items: flex-end;
  }
}

.back-call__phones-list {
  margin-right: 40px;
}
@media (max-width: 767px) {
  .back-call__phones-list {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.back-call__phone-item {
  margin-top: 0;
  margin-bottom: 15px;
}
.back-call__phone-item:last-child {
  margin-bottom: 0;
}

.back-call__phone-link {
  font-size: 16px;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .back-call__phone-link {
    font-size: 14px;
  }
}
@media (hover: hover) {
  .back-call__phone-link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.back-call__phone-link:active {
  opacity: 0.4;
}
.back-call__phone-link:focus-visible {
  outline: 2px solid #000000;
  outline-offset: 2px;
}

.social {
  display: grid;
  grid-template-columns: repeat(auto-fit, 45px);
  grid-gap: 20px;
}
@media (max-width: 1199px) {
  .social {
    justify-content: center;
  }
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  border: 2px solid #66c05d;
  border-radius: 50%;
}
@media (hover: hover) {
  .social__link:hover:not(.focus-visible) {
    border-color: #828282;
  }
  .social__link:hover:not(.focus-visible) svg {
    fill: #828282;
  }
}
.social__link:focus-visible {
  outline: 2px solid #000000;
  outline-offset: 2px;
}

.offer-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 25px 30px;
  text-align: center;
  background-color: #ffffff;
}

.offer-card__title {
  margin: 0 0 20px;
  font-size: 14px;
  font-weight: 400;
  color: #333333;
}

.offer-card__price {
  margin: 0 0 20px;
  font-size: 21px;
  line-height: 1.17;
  font-weight: 500;
  letter-spacing: 0.05em;
}

.offer-card__image {
  margin: 0 auto 20px;
  width: 100%;
  max-width: 125px;
  height: auto;
  object-fit: contain;
}
@media (max-width: 1199px) {
  .offer-card__image {
    width: calc(100% - 70px);
    max-width: 400px;
  }
}

.offer-card__buy {
  padding: 9px 38px 9px 37px;
}
.offer-card__buy::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
}

@media (max-width: 767px) {
  .header {
    transition: all 0.3s ease;
  }
}

@media (max-width: 767px) {
  .header__wrapper {
    display: grid;
    background-color: #ffffff;
    transition: all 0.3s ease;
  }
}

.header__navigation {
  padding: 12px 45px 11px;
  background-color: #efefef;
}
@media (max-width: 1199px) {
  .header__navigation {
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (max-width: 767px) {
  .header__navigation {
    order: 2;
    padding-right: 20px;
    padding-left: 20px;
    background-color: #ffffff;
    border-bottom: 1px solid #efefef;
  }
}

.header__catalog {
  padding: 25px 45px;
}
@media (max-width: 1199px) {
  .header__catalog {
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (max-width: 767px) {
  .header__catalog {
    order: 3;
    padding-right: 20px;
    padding-left: 20px;
  }
}

.header__main {
  padding: 20px 45px;
  border-bottom: 1px solid #efefef;
}
@media (max-width: 1199px) {
  .header__main {
    padding-right: 40px;
    padding-left: 40px;
  }
}
@media (max-width: 767px) {
  .header__main {
    order: 1;
    padding-right: 20px;
    padding-left: 20px;
  }
}

.header__main-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__navigation-toggle {
  display: none;
}

.header__logo {
  margin-right: 30px;
}
@media (max-width: 767px) {
  .header__logo {
    margin-right: 20px;
  }
}

@media (max-width: 767px) {
  .header {
    overflow: hidden;
    max-height: 80px;
  }
  .header--nojs {
    overflow: visible;
    max-height: unset;
  }
  .header--navigation-opened {
    position: absolute;
    z-index: 100;
    overflow-y: hidden;
    width: 100%;
    height: 100vh;
    max-height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
  }

  .header--navigation-opened .header__wrapper {
    overflow-y: auto;
    max-height: 100vh;
    transition: background-color 1s ease;
  }

  .header--navigation-opened .navigation-container .header__wrapper {
    display: grid;
  }

  .header__navigation-toggle {
    position: relative;
    width: 24px;
    height: 22px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: opacity 0.3s ease;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .header__navigation-toggle:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
@media (max-width: 767px) {
  .header--nojs .header__navigation-toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .header--navigation-closed .header__back-call {
    display: none;
  }
}
@media (max-width: 767px) {
  .header--navigation-closed .header__navigation-toggle {
    display: block;
  }
  .header--navigation-closed .header__navigation-toggle::before {
    position: absolute;
    z-index: 10;
    top: 4px;
    right: 0;
    left: 0;
    height: 2px;
    content: "";
    background-color: #000000;
    box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
  }
}
@media (max-width: 767px) {
  .header--navigation-opened .header__navigation-toggle {
    display: block;
  }
  .header--navigation-opened .header__navigation-toggle::before, .header--navigation-opened .header__navigation-toggle::after {
    position: absolute;
    top: 2px;
    right: 0;
    bottom: 2px;
    left: 0;
    width: 1.5px;
    margin: auto;
    content: "";
    background-color: #000000;
    transform: rotate(45deg);
  }
  .header--navigation-opened .header__navigation-toggle::after {
    transform: rotate(-45deg);
  }
}
.promo {
  overflow: hidden;
  height: 604px;
  max-height: 604px;
  padding: 0 45px;
  color: #ffffff;
  background-color: #66c05d;
}
@media (max-width: 1199px) {
  .promo {
    height: 504px;
    max-height: 504px;
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .promo {
    height: auto;
    max-height: unset;
    min-height: unset;
    padding: 20px 20px;
  }
}

.promo__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  height: 100%;
}
@media (max-width: 767px) {
  .promo__wrapper {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, auto);
  }
}

.promo__info {
  position: relative;
  z-index: 3;
  max-width: 325px;
}
@media (max-width: 1199px) {
  .promo__info {
    max-width: 308px;
  }
}

.promo__title {
  margin-top: 0;
  margin-bottom: 50px;
  font-size: 48px;
  font-weight: 700;
  line-height: 1.16;
}
@media (max-width: 1199px) {
  .promo__title {
    margin-bottom: 40px;
    font-size: 38px;
  }
}

.promo__text {
  margin-top: 0;
  margin-bottom: 15px;
}

.promo__detail-link {
  margin-top: 35px;
  padding-right: 44px;
  padding-left: 44px;
}
@media (max-width: 1199px) {
  .promo__detail-link {
    margin-top: 25px;
  }
}

.promo__image {
  position: relative;
  left: -68px;
  width: 548px;
  height: 548px;
}
.promo__image img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.promo__image::before, .promo__image::after {
  position: absolute;
  top: -46px;
  right: -46px;
  bottom: -46px;
  left: -46px;
  z-index: 2;
  content: "";
  background-color: #ffffff;
  border-radius: 50%;
  opacity: 0.4;
}
.promo__image::after {
  top: -138px;
  right: -138px;
  bottom: -138px;
  left: -138px;
  z-index: 1;
  opacity: 0.3;
}
@media (max-width: 1199px) {
  .promo__image {
    left: 0;
    width: 45.66vw;
    height: 45.66vw;
  }
  .promo__image::before {
    top: -36px;
    right: -36px;
    bottom: -36px;
    left: -36px;
  }
  .promo__image::after {
    top: -128px;
    right: -128px;
    bottom: -128px;
    left: -128px;
    z-index: 1;
    opacity: 0.3;
  }
}
@media (max-width: 767px) {
  .promo__image {
    justify-self: center;
    width: 87.5vw;
    max-width: 408px;
    min-width: 280px;
    height: 87.5vw;
    max-height: 408px;
    min-height: 280px;
    margin-top: 40px;
  }
  .promo__image::before {
    top: -24px;
    right: -24px;
    bottom: -24px;
    left: -24px;
  }
  .promo__image::after {
    top: -112px;
    right: -112px;
    bottom: -112px;
    left: -112px;
    z-index: 1;
    opacity: 0.3;
  }
}

.special-offers {
  padding: 80px 45px;
  background-color: #efefef;
}
@media (max-width: 1199px) {
  .special-offers {
    padding: 60px 40px;
  }
}
@media (max-width: 767px) {
  .special-offers {
    padding: 50px 20px;
  }
}

.special-offers__title {
  position: relative;
  margin-top: 0;
  margin-bottom: 38px;
  padding-right: 234px;
  font-size: 24px;
  line-height: 1.16;
  font-weight: 700;
}
.special-offers__title::after {
  position: absolute;
  top: -10px;
  right: 45px;
  width: 189px;
  height: 61px;
  background-image: url("../img/svg/car.svg");
  background-size: cover;
  content: "";
}
@media (max-width: 1199px) {
  .special-offers__title {
    margin-bottom: 30px;
    padding-right: 0;
    text-align: center;
  }
  .special-offers__title::after {
    display: none;
  }
}
@media (max-width: 767px) {
  .special-offers__title {
    margin-bottom: 26px;
    padding-right: 0;
    font-size: 20px;
  }
}

.special-offers__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  align-items: start;
}
@media (max-width: 1199px) {
  .special-offers__list {
    padding: 0 20px;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
  }
}
@media (max-width: 767px) {
  .special-offers__list {
    grid-template-columns: 1fr;
    grid-gap: 30px;
    max-width: 600px;
    margin: 0 auto;
    padding: 0;
  }
}

.presents-and-map {
  padding: 20px 45px 132px;
  background-color: #efefef;
}
@media (max-width: 1199px) {
  .presents-and-map {
    padding: 20px 40px 112px;
  }
}
@media (max-width: 767px) {
  .presents-and-map {
    padding: 20px 20px 90px;
  }
}

.presents-and-map__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
}
@media (max-width: 767px) {
  .presents-and-map__wrapper {
    grid-template-columns: 1fr;
  }
}

.presents-and-map__section {
  position: relative;
  padding: 35px 40px;
  color: #ffffff;
  background-color: #66c05d;
}
.presents-and-map__section--presents {
  padding-right: 235px;
}
.presents-and-map__section--presents::after {
  position: absolute;
  top: 0;
  right: 45px;
  bottom: 0;
  margin: auto 0;
  width: 140px;
  height: 140px;
  content: "";
  background-image: url("../img/svg/email.svg");
  background-size: cover;
}
.presents-and-map__section--map {
  padding-right: 320px;
}
@media (max-width: 1199px) {
  .presents-and-map__section {
    padding-right: 40px;
    padding-top: 195px;
  }
  .presents-and-map__section--presents::after {
    position: absolute;
    top: 35px;
    right: 0;
    left: 40px;
    margin: 0 auto 0 0;
    width: 140px;
    height: 140px;
    content: "";
    background-image: url("../img/svg/email.svg");
    background-size: cover;
  }
}
@media (max-width: 767px) {
  .presents-and-map__section {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .presents-and-map__section--presents::after {
    position: absolute;
    top: 35px;
    right: 0;
    left: 0;
    margin: 0 auto;
    width: 140px;
    height: 140px;
    content: "";
    background-image: url("../img/svg/email.svg");
    background-size: cover;
  }
}

.presents-and-map__title {
  margin: 0 0 22px;
  font-size: 18px;
  line-height: 1.16;
  font-weight: 700;
}
@media (max-width: 767px) {
  .presents-and-map__title {
    text-align: center;
  }
}

.presents-and-map__text {
  margin: 0 0 25px;
}
@media (max-width: 767px) {
  .presents-and-map__text {
    text-align: center;
  }
}

.presents-and-map__email {
  padding: 7px 25px;
  color: #000000;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 30px;
  transition: opacity 0.3s ease;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.16;
  font-weight: 400;
}
.presents-and-map__email::placeholder {
  font-size: 14px;
  color: #c4c4c4;
}
@media (hover: hover) {
  .presents-and-map__email:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.presents-and-map__email:focus-visible {
  outline: 2px solid #000000;
  outline-offset: 2px;
}
@media (max-width: 767px) {
  .presents-and-map__email {
    display: block;
    width: 100%;
    max-width: 225px;
    margin: 0 auto;
  }
}

.presents-and-map__map-link {
  padding-top: 8px;
  padding-bottom: 8px;
}

.contacts {
  padding: 37px 45px 38px;
}
@media (max-width: 1199px) {
  .contacts {
    padding: 30px 40px 30px;
  }
}
@media (max-width: 767px) {
  .contacts {
    padding: 30px 20px 30px;
  }
}

.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
@media (max-width: 1199px) {
  .contacts__wrapper {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.contacts__social {
  width: 325px;
}
@media (max-width: 1199px) {
  .contacts__social {
    width: 100%;
    margin-bottom: 25px;
  }
}

.contacts__address {
  margin-top: 15px;
  line-height: 1.16;
}
.contacts__address address {
